  "welcomeMailBody1_msg": "Sretni smo to ste se prijavili za Tutanota servis, i estitamo na vaem novom ifriranom potanskom e-sanduiu. Samo vi moete deifrirati svoje e-mailove i kontakte koristei vau zaporku. Molimo pobrinite se da ne izgubite zaporku jer vam je mi ne moemo resetirati.",
  "welcomeMailBody2_msg": "Pozovite vae prijatelje na Tutanota-u, te moete slati ifrirane e-mailove dodirom vaeg prsta. Takoer moete ifrirati e-mailove koje aljete vanjskim primateljima s lozinkom. Tutanota ifrira naslov, sadraj i privitke automatski. ak i neifrirani e-mailovi se spremaju ifrirano na naim serverima. Ako elite poduprijeti nau borbu za privatnost, moete nadograditi na Premium za samo 1 Euro mjeseno.",
