  "welcomeMailBody6_msg": "P.P.S. Molimo ne aljite zahtjeve za dodatne opcije preko e-maila, jer se elimo fokusirati na razvoj. Ako uvidite da nedostaje vana opcija, moete glasati ovdje: https://tutanota.uservoice.com",
