  "welcomeMailBody1_msg": "Diaugiams, jog usiregistravote Tutanota ir sveikiname jus prisijungus prie ifruojamos pato duts.Tik js galite atifruoti turimus laikus ir kontaktus, pasinaudoj savo slaptaodiu. sitikinkite, jog nepamesite slaptaodio, nes jo atkurti mes negalsime.",
  "welcomeMailBody2_msg": "Pakvieskite savo draugus prisijungti prie Tutanota ir galsite sisti nuo js kompiuterio iki pat gavjo ifruotus laikus vienu prisilietimu. Pasinaudoj slaptaodiu js taip pat galite uifruoti laikus ioriniams gavjams. Tutanota automatikai uifruoja tem, turin ir prisegtus failus. Be to, netgi neifruoti laikai ms serveriuose yra saugomi tik ifruotai.",
  "welcomeMailBody3_msg": "Parodyk pasauliui, kad saugai savo privatum:",
  "welcomeMailBody4_msg": "Js Tutanota komanda",
  "welcomeMailBody5_msg": "P.S.: Tutanota yra atviro kodo, todl galite laisvai patikrinti ir perirti ieities kod:",
  "welcomeMailBody6_msg": "PPS: Nepraykite nauj galimybi  individualiai elektroniniu patu, nes mes nortume sutelkti vis savo laik programos tobulinimui. Jei radote svarbi trkstam funkcij, galite balsuoti ia:",
  "welcomeMailBodyTweetText_msg": "K tik pasijungiau savo Tutanota paskyr. Dabar a saugau savo laikus nuo smalsi aki! Prisijunkite ir Js,",
  "welcomeMailSubject_msg": "Jauskits rams: Js duomenys iliks privats.",
